import React, { Component } from 'react';

class LoginPage extends Component {

    constructor(props) {
        super(props);

        this.passwordInput;
        this.passwordInputRef = (ref) => this.passwordInput = ref;

        this.rememberCheckbox;
        this.rememberCheckboxRef = (ref) => this.rememberCheckbox = ref;
        this.loginHandler = this.props.loginHandler;

        this.state = {
            pwdMessage: '',
            rememberMe: false,
        }
    }


    sendPassword = (event) => {
        event.preventDefault();

        const pwd = this.passwordInput.value;
        const response = this.loginHandler(pwd);

        response
            .then(res => {
                console.log('sendPassword res', res);
                if (this.state.rememberMe) {
                    localStorage.rememberPassword = pwd;
                }
            })
            .catch(err => {
                console.log('sendPassword err', err);
                this.passwordInput.value = null;
                this.setState({
                    pwdMessage: 'Введёный пароль не подходит!'
                });
            });
    }

    changeRemember = (event) => {
        this.setState({rememberMe: !this.state.rememberMe});
    }


    render() {
        const containerCss = {
            display: 'flex', 
            height: '100vh',
            justifyContent: 'center',
            alignItems: 'center'
        }
        return (
            <div className="container" style={containerCss}>
                <div className="row" style={{width: '100%'}}>
                    <div className="col-sm-6 col-sm-offset-3">
                    
                        <form className="volunteer-reg-form" onSubmit={this.sendPassword}>
                            <h3 className="well">Добро пожаловать</h3>

                            <div className="row">
                                <div className="col-sm-12 form-group">
                                    <label >
                                        Введите пароль:
                                    </label>

                                    <input className="form-control" placeholder="Пароль" ref={this.passwordInputRef} />
                                </div>
                            </div>


                            <div className="row">
                                <div className="col-sm-12 form-group">
                                    <label >
                                        Запомнить меня 
                                        <input 
                                            className="checkbox"
                                            type="checkbox"
                                            ref={this.rememberCheckboxRef} 
                                            checked={this.state.rememberMe}
                                            onChange={this.changeRemember}
                                            style={{
                                                display: 'inline-block',
                                                height: 'auto',
                                                marginLeft: '15px',
                                            }}
                                        />
                                    </label>

                                </div>
                            </div>


                            <div className="row">
                                <div className="col-sm-12 form-group">
                                    <button className="btn btn-lg btn-primary ulockd-btn-thm2 ulockd-mrgn1220" type="submit">Отправить</button>
                                </div>
                            </div>
                            

                            <div className="row">
                                <div className="col-sm-12 form-group">
                                    {this.state.pwdMessage && (
                                        <p className="text-danger">{this.state.pwdMessage}</p>
                                    )}
                                </div>
                            </div>


                        </form>

                    </div>
                </div>
            </div>
        )
    }
}

export default LoginPage;


